@import url(https://fonts.googleapis.com/css2?family=Source+Serif+Pro:ital,wght@0,300;1,300;1,400&display=swap);
body{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: #a4dbe996;
}

* {
    font-family: 'Source Serif Pro';
    font-size: 18px;
}

.container {
    top: 50%;
    left: 50%;
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    margin: 0 auto;
    width: 256px;
    text-align: center;
    border: 10px solid #218199ad;
    background: #101010;
    border-radius: 5px;
}


.keypad{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-auto-rows: minmax(60px, auto);

}

input[type="text"]{
    height: 75px;
    width: 250px;
    background-color: #10111600;
    border: 0px;
    color: #ffffff;
    text-align: right;
    font: 25px;
    font-weight: 500;
    letter-spacing: 1px;

}

#clear{
    grid-column: 1/3;
    grid-row: 1;
    color: #262834;

}

#result{
    grid-column: 3/5;
    grid-row: 5;
    color:antiquewhite
}

#backspace{
    color: #262834
}

button{
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background: #262834 ;
    color: #ffffff;
    font-weight: 500;

}

input:focus,
input:active,
button:focus,
button:active {
    outline: none;
     
}
.highlight1{
    background: #aaa994;
}

.highlight{
    background: #0a84aa;
}
